export default class Api {
  constructor(data) {
    this._baseUrl = data.serverUrl;
    this._token = data.token;
    this._cohortId = data.cohortId;
  }

  _checkResponse(res) {
    if (res.ok) {
      return res.json();
    } else {
      return Promise.reject(
        `Error: ${res.status} - ${res.statusText}`
      );
    }
  }

  getUserInfo() {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/users/me`, {
      headers: {
        authorization: `Bearer ${this._token}`,
      },
    }).then((res) => this._checkResponse(res));
  }

  getInitialCards() {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/cards`, {
      headers: {
        authorization: `Bearer ${this._token}`,
      },
    }).then((res) => this._checkResponse(res));
  }

  editAvatar(data) {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/users/me/avatar`, {
      method: "PATCH",
      headers: {
        authorization: `Bearer ${this._token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        avatar: data.avatar,
      }),
    }).then((res) => this._checkResponse(res));
  }

  editProfile(data) {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/users/me`, {
      method: "PATCH",
      headers: {
        authorization: `Bearer ${this._token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: data.name,
        about: data.job,
      }),
    }).then((res) => this._checkResponse(res));
  }

  addNewCard(data) {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/cards`, {
      method: "POST",
      headers: {
        authorization: `Bearer ${this._token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: data.name,
        link: data.link,
      }),
    }).then((res) => this._checkResponse(res));
  }

  deleteCard(data) {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/cards/${data}`, {
      method: "DELETE",
      headers: {
        authorization: `Bearer ${this._token}`,
      },
    }).then((res) => this._checkResponse(res));
  }

  addCardLike(data) {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/cards/likes/${data}`, {
      method: "PUT",
      headers: {
        authorization: `Bearer ${this._token}`,
      },
    }).then((res) => this._checkResponse(res));
  }

  deleteCardLike(data) {
    return fetch(`${this._baseUrl}/v1/${this._cohortId}/cards/likes/${data}`, {
      method: "DELETE",
      headers: {
        authorization: `Bearer ${this._token}`,
      },
    }).then((res) => this._checkResponse(res));
  }
}

const api = new Api({
  serverUrl: "https://mesto.nomoreparties.co",
  token: "43bc2f0f-65e6-40f5-a20b-329dc405ffe8",
  cohortId: "cohort-66",
});

export { api };